.products {
  background-color: transparent;
}
.products__container {
  padding-top: 60px;
  padding-bottom: 60px;
  text-align: center;
}

.product__subtitle {
  text-align: center;
  margin-bottom: 12px;

  @include font(800, 18px, 22px);
  font-family: 'Montserrat';
  text-transform: uppercase;

  @include media('tablet') {
    font-size: 22px;
    line-height: calc(27 / 22);
  }

  @include media('desktop') {
    font-size: 30px;
    line-height: calc(37 / 30);
  }
}

.product__title {
  text-align: center;
  margin-bottom: 148px;

    @include font(400, 30px, 38px);
    font-family: 'Seymour One';
    letter-spacing: 0.04em;
    text-transform: uppercase;

  @include media('tablet') {
    margin-bottom: 200px;
    font-size: 34px;
    line-height: calc(43 / 34);
  }

  @include media('desktop') {
    margin-bottom: 300px;
    font-size: 36px;
    line-height: calc(45 / 36);
  }
}

.products__list {
  @include flex(flex, center, center, column);
  gap: 164px;

  @include media('tablet') {
    flex-direction: row;
    gap: 16px;
  }

  @include media('desktop') {
    gap: 32px;
  }
}

.card {
  @include flex(flex, center, center, column);
  flex-shrink: 0;
  position: relative;
  width: 280px;
  height: 420px;
  border-radius: 24px;
  padding-top: 124px;
  padding-bottom: 40px;
  padding-left: 40px;
  padding-right: 40px;
  @include media('tablet') {
    width: 225px;
    height: 430px;
    padding-top: 110px;
    padding-left: 26px;
    padding-right: 26px;
    padding-bottom: 40px;
  }

  @include media('desktop') {
    width: 370px;
    height: 530px;
    padding-top: 166px;
    padding-left: 60px;
    padding-right: 60px;
    padding-bottom: 40px;
  }
}

.card--cream {
  background-color: $IceCreamColor;

  &::before {
    content: '';

    position: absolute;

    width: 90%;
    height: 65%;

    top: 0;
    left: 50%;
    transform: translate(-50%, -50%);

    // transform: translateX(-50%);

    background-image: url('../images/products/cream-s-min.png');
    background-repeat: no-repeat;

    @media (min-device-pixel-ratio: 2),
      (-webkit-min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
      background-size: 252px 273px;
      background-image: url('../images/products/cream-s@2x-min.png');
    }

    @include media('tablet') {
      width: 107%;
      height: 55%;
      background-image: url('../images/products/cream-m-min.png');
      position: center;

      @media (min-device-pixel-ratio: 2),
        (-webkit-min-device-pixel-ratio: 2),
        (min-resolution: 192dpi),
        (min-resolution: 2dppx) {
        background-size: 240px 236px;
        background-image: url('../images/products/cream-m@2x-min.png');
      }
    }

    @include media('desktop') {
      width: 99%;
      height: 70%;
      background-image: url('../images/products/cream-l-min.png');
      position: center;

      @media (min-device-pixel-ratio: 2),
        (-webkit-min-device-pixel-ratio: 2),
        (min-resolution: 192dpi),
        (min-resolution: 2dppx) {
        background-size: 359px 371px;
        background-image: url('../images/products/cream-l@2x-min.png');
      }
    }
  }
}

