.card__title {
  margin-bottom: 40px;
  text-align: center;

  @include font(700, 20px, 24px);
  font-family: 'Montserrat';
  letter-spacing: 0.04em;
  text-transform: uppercase;

  @include media('tablet') {
    font-size: 22px;
    line-height: calc(27 / 22);
  }

  @include media('desktop') {
    font-size: 30px;
    line-height: calc(37 / 30);
    margin-bottom: 65px;
  }
}

.card__description {
  display: block;
  text-align: center;
  overflow: hidden;
  margin-right: auto;
  margin-left: auto;

  @include font(400, 14px, 25px);
  font-family: 'Montserrat';

  text-align: center;
  letter-spacing: 0.04em;

  @include media('desktop') {
    font-size: 16px;
    line-height: calc(28 / 16);
    margin-bottom: 65px;
  }
}

.products__button {
  display: inline-block;
  width: 50px;
  height: 20px;

  margin-left: auto;
  margin-right: auto;

  margin-top: auto;
  background-color: transparent;
  cursor: pointer;
}

.array__icon {
  fill: $PrimaryTextColor;
  width: 40px;
  height: 15px;
  transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.products__button:focus .array__icon {
  transform: scaleY(1.5);
}

.products__button:hover .array__icon {
  transform: scaleX(1.2);
}
